.tableRow {
    display: flex;
    align-items: stretch;
    align-content: center;
    justify-content: center;
    margin: 2px 0px;
    padding: 6px;
    border-bottom: 1px solid gray;
    min-width: 550px;
}

.tableRow:hover {
    background-color: lightgray;
    cursor: pointer;
}

.tableActiveRow {
    display: flex;
    align-items: stretch;
    align-content: center;
    justify-content: center;
    margin: 2px 0px;
    padding: 6px;
    border-bottom: 1px solid gray;
    min-width: 550px;
    background-color: #0B74A4;
    color: white;
}

.tableActiveRow:hover {
    background-color: lightgray;
    color: black;
    cursor: pointer;
}

.headerRow {
    display: flex;
    align-items: stretch;
    align-content: center;
    justify-content: center;
    margin: 2px 0px;
    padding: 6px;
    border-bottom: 1px solid gray;
    min-width: 550px;
}
 
.headerCell {
    flex-grow: 1;
    margin: auto;
    font-size: larger;
    font-weight: bold;
    white-space: nowrap;
}

.headerMenu {
    padding: 2px 4px;
    display: inline-flex;
    align-content: center;
    align-items: center;
}

.headerMenu:hover {
    padding: 2px 10px;
    background-color: #0B74A4;
    color: white;
}

.headerCell:hover {
    cursor: pointer;
}

.tableCell {
    flex-grow: 1;
    /* margin: auto; */
    padding-left: 4px;
    white-space: nowrap;
}

