.App {
  font-family: sans-serif;
  text-align: center;
}

.Header {
  position: fixed;
  top: 0px;
  left: 0px;
  height: 100%;
  width:100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
}

.topMenu {
  padding-left: 20px;
  padding-right: 20px;
  height: 46px;
  font-size: 18px;
  color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
}

.topMenu:hover {
  background-color: lightgray;
  color: #0B74A4;
}

.dropMenu {
  padding-left: 20px;
  padding-right: 20px;
  height: 46px;
  font-size: 18px;
  color: #FFFFFF;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  border-radius: 3px;
}

.dropMenu:hover {
  background-color: lightgray;
  color: black;
}

.slideIn {
  padding: 10px;;
  margin: 10px;
  background-color: #EEEEEE;
  color: #000000;
}

.slideIn:hover {
  background-color: #EEEEEE;
}

.loading {
 width: 300px;
 height: 200px;
 text-align: center;
 background-color: lightskyblue;
}

.fadeOut {
  width: 100%;
  height: 100%;
  background-color: lightskyblue;
  animation: fadeIn ease 5s;
  -webkit-animation: fadeIn ease 5s;
  -moz-animation: fadeIn ease 5s;
  -o-animation: fadeIn ease 5s;
  -ms-animation: fadeIn ease 5s;
  animation-fill-mode: forwards;
}


@keyframes fadeIn {
  0% {
    width: 100%;
    height: 100%;
  }
  100% {
    width: 0%;
    height: 0%;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity:1;
  }
  100% {
    opacity:0;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity:1;
  }
  100% {
    opacity:0;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity:1;
  }
  100% {
    opacity:0;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity:1;
  }
  100% {
    opacity:0;
  }
}


.panel {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #222;
  border: 1px solid #555;
  box-sizing: border-box;
}

.panelProject {
  height: 100%;
  display: flex;
  justify-content: top;
  align-items: left;
  background-color: #222;
  border: 0px solid #555;
  box-sizing: border-box;
}
